header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

h5 {
    font-size: 1rem;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}


/* ========================== CTA ========================= */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 2rem;
    justify-content: center;
}

/* ========================== SOCIALS ========================= */
.header__socials {
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;

}

.header__socials::after{
    content: '';
    width: 1.4px;
    height: 4rem;
    background: var(--color-primary);
}

/* ========================== ME ========================= */
.me {
    background: linear-gradient(rgba(9,4,45), transparent);
    width: 20rem;
    height: 24rem;
    position: absolute;
    left: calc(50% - 9.5rem);
    margin-top: 4rem;
    border-radius: 15rem 15rem 0rem 0rem;
    overflow: hidden;
    padding: 0rem 0rem 0rem 0rem;
}

/* =========================== MEDIUM DEVICES ====================== */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

}



/* =========================== SMALL DEVICES ====================== */

@media screen and (max-width: 600px) { 
    header {
        height: 100vh;
    }

    .header__socials {
        display: none;
    }
} 





