.services__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 8rem;
}

.service {
    background: rgb(1, 42, 99);
    /* background: rgb(15, 53, 110); */
    /* border-radius: 2rem 2rem 2rem 2rem; */
    border-radius: 2rem;
    border: 1px solid transparent;
    height: fit-content;
    transition: var(--transition);

}

.service:hover{
    background: rgb(15, 53, 110);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service__head {
    background:  rgb(32, 92, 175);
    padding: 2rem;
    border-radius: 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);

}

.service__head h3 {   
    color: white;
    font-size: 1.2rem;
    text-align: center;

}

.service__list {
    padding: 2rem; 
}

.service__list li {
    display: flex;
    font-size: 1.2rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service__list_icon {
    color: white;
    margin-top: 2px;
    font-size: auto;

}

.service__list p {
    font-size: 1rem;
}

.fuzuli {
    color: transparent;
}

/* =========================== MEDIUM DEVICES ====================== */

@media screen and (max-width: 1024px) {
    .services__container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }


    .service {
        height: auto;
    }

}

/* =========================== SMALL DEVICES ====================== */

@media screen and (max-width: 600px) {
    .services__container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    
}