footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 2.8rem;
}

footer a {
    color: black;
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 4rem;
    font-size: 1.1rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
    font-size: 1.2rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: rgb(15, 53, 110);
    border-color: var(--color-primary-variant);
    border-color: var(--color-bg);
}

.footer__copyright {
    font-size: 0.9rem;
    color: black;
    position: relative;
    top: -50px;
    /* margin-top: 5rem; */
}

/* =========================== SMALL DEVICES ====================== */

@media screen and (max-width: 600) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}
